






























@use 'sass:color';

.search {
    width: 100%;
    display: flex;

    .textfield {
        height: 100%;
        padding-left: 1rem;
        flex: 1;

        &::placeholder {
            color: var(--primary);
            opacity: 0.54;
        }
    }

    .btn {
        width: 2.5rem;
    }
}
